import React, { Component } from 'react';

import classes from './Portfolio.css';
import Card from '../../components/UI/Card/Card';
import Modal from '../../components/UI/Modal/Modal';
import PopupWindow from '../../components/UI/PopupWindow/PopupWindow';
import CardList from '../../components/UI/CardList/CardList';
import TopBar from '../../components/UI/TopBar/TopBar';
import Aux from '../../hoc/Aux';
import SelfIntro from '../../components/SelfIntro/SelfIntro';
import BusinessCard from '../../components/BusinessCard/BusinessCard';
import ContactInfo from '../../components/ContactInfo/ContactInfo';

import MemoryGame from '../../assets/MemoryGame.png';
import Typing from '../../assets/Type.png';
import ChatRoomPhoto from '../../assets/chatRoom.png';
import MyBookShelf from '../../assets/mybookshelf.png';
import Burger from '../../assets/burger.png';
import InfoBar from '../../assets/InformationTagFull.png';
import ArcadeGame from '../../assets/arcade.png';

class Portfolio extends Component {
  state = {
    selfIntroTitle: 'Hello, this is Jingyi, Nice to meet you!',
    selfIntro: 'I\'m a New Graduate student recently completed my Master degree. ' 
              + 'I love coding and enjoy creating & developing cool products. '
              + 'Also, I am always curious about new tech and keep learning to improve myself day by day.',
    bcardInfo: [
      {
        logo: 'fas fa-code',
        attrTitle: 'Skills',
        attrList: [
          {
            attrname: 'Language',
            info: 'Java, JavaScript(ES5/ES6), Some Scala and Python'
          },
          {
            attrname: 'Front-end',
            info: 'React, Redux, Bootstrap, HTML5, CSS3, Responsice Design'
          },
          {
            attrname: 'Back-end',
            info: 'Node.js, Express.js, MySQL, MongoDB, RESTful API'
          },
          {
            attrname: 'Cloud',
            info: 'AWS, GCP, Firebase, Heroku'
          },
          {
            attrname: 'Tools',
            info: 'Mocha, Linux/maxOS, Github, Chrome, VS Code, Postman'
          },
        ]
      },
      {
        logo: 'fas fa-file',
        attrTitle: 'Experience',
        attrList: [
          {
            attrname: 'Working Experience',
            info: 'Software Engineer intern in one of the largest internet compaies in China.'
          },
          {
            attrname: 'Education',
            info: 'MS in Computer Engineering (University of Florida)'
          },
          {
            attrname: null,
            info: 'Bachelor in Information System and Information Management (China)'
          },
          {
            attrname: 'Certification',
            info: 'Nanodegree of Front-end Web Developer (Udacity)'
          },
        ]
      }
    ],
    webApplication: [
      {
        name: 'Customize Burger',
        description: 'Customize your burger, add your favorite ingredients and place the order!',
        information: 'Burger order web application. User can choose and add their favorite ingredients to customize their burgers. ' +
        'The system will count current burger and the price automatically. In check out page, user can input their information and then, Check out! ' +
        'Data sore on Firebase\'s realtime database.',
        techs: ['React.js', 'Redux', 'HTML5', 'CSS3', 'JavaScript(ES6)', 'AJAX(axios)', 'Firebase', 'Responsive Design'],
        photo: Burger,
        github: 'https://github.com/dingjy94/BurgerApplication',
        link: 'https://reactburger-d37b5.firebaseapp.com/'
      },
      {
        name: 'My BookShelf',
        description: 'Manage books you want to read!',
        information: 'BookShelf application allow user to manage book status(Reading, Readed, Want to Read),' + 
        'search and add new books. Get and store data through Udacity\'s book API using AJAX. Course project of ' +
        'Udacity Front-end Web Developer Nanodegree',
        techs: ['React.js', 'HTML5', 'CSS3', 'JavaScript(ES6)', 'AJAX', 'Firebase'],
        photo: MyBookShelf,
        github: 'https://github.com/dingjy94/reactnd-project-myreads-starter',
        link: 'https://mybookshelf-71f8e.firebaseapp.com/'
      },
      {
        name: 'The Chat',
        description: 'Chat with chat room members!',
        information: 'Real-time chat room application allows users to select rooms and chat with room members or share their locations.',
        techs: ['Socket.io', 'Express.js', 'HTML5', 'CSS3', 'JavaScript(jQuery)', 'Herouku'],
        photo: ChatRoomPhoto,
        github: 'https://github.com/dingjy94/NodeChatApp',
        link: 'https://serene-sea-25990.herokuapp.com'
      }
    ],
    javaScript: [
      {
        name: 'Memory Game',
        description: 'Click, remember, find the pairs!',
        information: 'The traditional card memory game. Click cards and find the matching pair. After you find all 8 pairs, the game will finish. And also, you can click the refresh button to refresh the game (it will shuffle the cards). This is the course project of Udacity front-end development nenodegree.',
        techs: ['HTML5', 'CSS3', 'JavaScript(ES5)'],
        photo: MemoryGame,
        github: 'https://github.com/dingjy94/memory-game',
        link: 'http://jingyid.me/memory-game/'
      },
      {
        name: 'Arcade Game',
        description: 'Run! Run! Avoid enemies and run to the river.',
        information: 'In this game you have a Player and Enemies (Bugs). The goal of the player is to reach the water, without colliding into any one of the enemies. The player can move left, right, up and down. The enemies move in varying speeds on the paved block portion of the scene. Once a the player collides with an enemy, the game is reset and the player moves back to the start square. Once the player reaches the water the game is won.',
        techs: ['HTML5', 'CSS3', 'JavaScript(ES6)'],
        photo: ArcadeGame,
        github: 'https://github.com/dingjy94/Arcade-game',
        link: 'http://jingyid.me/Arcade-game/'
      }
    ],
    ui: [
      {
        name: 'Information Bar',
        description: 'Responsive user information bar',
        information: 'Responsive designed user information bar for social network website. Include button group, activity bar...',
        techs: ['HTML5', 'CSS3', 'Responsive Design'],
        photo: InfoBar,
        github: 'https://github.com/dingjy94/UICollections/tree/master/components/InformationTag',
        link: 'https://codepen.io/dingjy94/pen/JaJxgL/'
      },
      {
        name: 'Typing Animation',
        description: 'Typying animation using pure CSS',
        information: 'Typing animation created by HTML & CSS, the one used on this cover page. Care the length of paragraphs.',
        techs: ['HTML5', 'CSS3'],
        photo: Typing,
        github: 'https://github.com/dingjy94/MyUI/tree/master/TypingAnimation',
        link: 'https://codepen.io/dingjy94/pen/PdpVeo'
      }
    ],
    selectedCard: null,
    showPop: false,
    contactShow: false
  }

  CardClickHandler = (app) => {
    console.log(app);
    this.setState({showPop: true, selectedCard: app});
  }

  PopCloseHandler = () => {
    this.setState({showPop: false});
  }

  ContactShowHandler = () => {
    this.setState({contactShow: true});
    console.log(this.state.contactShow);
  }

  ContactCloseHandler = () => {
    this.setState({contactShow: false});
  }

  render() {
    let attachedClasses = [classes.Portfolio, classes.Close];
    if (this.props.show) {
      attachedClasses = [classes.Portfolio];
    }
    let popupWindow = null;
    if (this.state.selectedCard) {
      popupWindow = <PopupWindow selected={this.state.selectedCard}/>
    }
    return (
      <Aux>
        <main className={attachedClasses.join(' ')}>
          <div >
            <TopBar contact={this.ContactShowHandler} />
            <Modal show={this.state.showPop} modalClosed={this.PopCloseHandler}>
              {popupWindow}
            </Modal>  
            <Modal show={this.state.contactShow} modalClosed={this.ContactCloseHandler}>
              <ContactInfo />
            </Modal> 
            <SelfIntro introTitle={this.state.selfIntroTitle} intro={this.state.selfIntro} />
            <div className={classes.BusinessCard}>
              <BusinessCard bcardInfo={this.state.bcardInfo} />
            </div>
            <CardList 
              title='Web Application'
              subtitle='Web applications with complete UI, fetch data from back-end server or third party API.' 
              >
              {this.state.webApplication.map((app) => (
                <li key={app.name}>
                  <Card
                    key={app.name}
                    photo={app.photo}
                    name={app.name}
                    description={app.description}
                    clicked={() => this.CardClickHandler(app)} />
                </li>
              ))}
            </CardList>
            <CardList
              title='JavaScript Playgroud'
              subtitle='JavaScript! JavaScript! Some interesting projects play with JS.'
            >
              {this.state.javaScript.map((app) => (
                <li key={app.name}>
                  <Card
                    key={app.name}
                    photo={app.photo}
                    name={app.name}
                    description={app.description}
                    clicked={() => this.CardClickHandler(app)} />
                </li>
              ))}
            </CardList>
            <CardList
              title='UI Collections'
              subtitle='My components & UI collections. Beauty of CSS.'
            >
              {this.state.ui.map((app) => (
                <li key={app.name}>
                  <Card
                    key={app.name}
                    photo={app.photo}
                    name={app.name}
                    description={app.description}
                    clicked={() => this.CardClickHandler(app)} />
                </li>
              ))}
            </CardList>
          </div>
        </main>
      </Aux>
    );
  }
}

export default Portfolio;


